import { useState } from 'react';
import { useForm } from 'react-hook-form';
import { Link } from 'react-router-dom';
import Admission from '../../containers/Admission';
import Form from '../../components/ui/Form';
import Input from '../../components/smart/Input';
import SimpleButton from '../../components/ui/SimpleButton';
import ResponseError from '../../core/elementaryEntities/ResponseError';
import { useAuthContext } from '../../core/provider/AuthProvider';

interface RegistrationProps {
    isMobile: boolean;
    goHome: Function;
}
const Registration = ({ isMobile, goHome }: RegistrationProps) => {
    const [error, setError] = useState<string>('');
    const authContext = useAuthContext();
    const {
        register,
        formState: {
            errors,
            isValid
        },
        handleSubmit
    } = useForm({
        mode: 'onBlur'
    });

    const onSubmitHandler = (data: any) => {
        authContext.registration(data.email, data.username, data.password, (error: ResponseError) => {
            if (error) {
                setError(error.error)
            } else {
                setError('')
                goHome();
            }
        });
    };

    return (
        <Admission
            isMobile={isMobile}
            error={error}
            title=''
            img={require('./registration.png')}
        >
            <Form
                onSubmit={handleSubmit(onSubmitHandler)}
                className='admission-page__form'
            >
                <Input
                    label=':'
                    type='email'
                    errorMessage={errors?.email?.message}
                    isMobile={isMobile}
                    {...register('email', {
                        required: '   ',
                        minLength: {
                            value: 3,
                            message: ' 3 '
                        }
                    })}
                >
                    email@email.com
                </Input>

                <Input
                    label=':'
                    type='text'
                    errorMessage={errors?.username?.message}
                    isMobile={isMobile}
                    {...register('username', {
                        required: '   ',
                        minLength: {
                            value: 3,
                            message: ' 3 '
                        }
                    })}
                >
                    
                </Input>

                <Input
                    label=':'
                    type='password'
                    errorMessage={errors?.password?.message}
                    isMobile={isMobile}
                    {...register('password', {
                        required: '   ',
                        minLength: {
                            value: 6,
                            message: ' 6 '
                        }
                    })}
                >
                    1234567
                </Input>

                <Admission.Footer>
                    <SimpleButton
                        isDisabled={!isValid}
                        isMobile={isMobile}
                        isFill={isMobile}
                        type='submit'
                        value=''
                        text=''
                    />

                    <Link to='/auth'>  </Link>
                </Admission.Footer>
            </Form>
        </Admission>
    );
};

export default Registration;