import { getCorrectString } from './word';

const createRegister = (register: any, name: string, minLength: number) => {
    return register(name, {
        required: '   ',
        minLength: {
            value: minLength,
            message: ` ${minLength} ${getCorrectString('', minLength)}`
        }
    })
};

export {
    createRegister
}